/* css reset*/
* {
    box-sizing: border-box;
}

*:before,
*:after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

ul,
ol {
    list-style: none;
}

img {
    max-width: 100%;
    max-height: 100%;
}

body {
    background: #eee;
}

a {
    color: inherit;
    text-decoration: none;
}

.globalHeader {

    margin: 20px;

}

@media (min-width:500px) {
    .globalHeader {

        margin: 60px 0 100px;

    }
}

.searchForm {
    display: flex;
    justify-content: space-between;
}

@media(min-width:500px) {
    .searchForm {
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}

.searchForm>input {
    width: 100%;
    margin-right: 10px;
    height: 40px;
    padding: 0 10px;
    border: none;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.searchForm>button {
    white-space: nowrap;
    padding: 0 28px;
    border: none;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: #0282b9;
    color: white;
    font-size: 16px;
}

.globalMain {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}

@media (min-width:500px) {
    .siteList {
        margin-left: 0px;
        margin-right: -25px;
        justify-content: flex-start;
    }
}

.siteList>li {
    margin-bottom: 10px;


}

@media (min-width:500px) {
    .siteList>li {
        margin-right: 25px;
    }
}

.siteList .site {

    width: 160px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: white;
    padding: 20px 0;
    position: relative;
    cursor: pointer;
}

.siteList .site>.logo {

    width: 64;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
}

.siteList .site>.link {
    font-size: 14px;
    margin-top: 4px;
}

.siteList .site>.close {

    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    position: absolute;
    right: 0px;
    top: 0px;
    cursor: default;

}





.siteList .addButton {
    width: 160px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: white;
    padding: 20px 0;
}

.siteList .addButton>.icon {

    width: 64;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;

}

.siteList .addButton>.text {
    font-size: 14px;
    margin-top: 4px;

}